.content-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.day {
    height: 800px;
    width: 13%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

@media all and (max-device-width: 500px) {
    .day {
        height: 1200px !important;
    }
}

.class {
    background-color: lightgray;
    width: 96%;
    height: 17%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}